.header {
  width: 100%;
  color: #fff;
  background-image: url("header-main.b65359ce.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding-top: 40px;
  padding-bottom: 44px;
}

.header__main {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 54px;
  display: flex;
}

.header__title {
  align-items: center;
  gap: 8px;
  display: flex;
}

.header__name {
  font-size: 30px;
  display: none;
}

@media screen and (min-width: 768px) {
  .header__name {
    display: block;
  }
}

.header__nav {
  text-transform: uppercase;
  color: #fff;
  gap: 40px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.header__active {
  position: relative;
}

.header__active:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: red;
  position: absolute;
  top: 14px;
  left: 0;
}

.header__form {
  max-width: 320px;
  margin: 0 auto;
  position: relative;
}

.header__input {
  width: 100%;
  color: #fff;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #fff;
  padding: 0;
}

.header__input::placeholder {
  color: #fff;
}

.header__icon {
  position: absolute;
  top: .2rem;
  right: 0;
}

.header--library {
  background-image: url("header-library.bfc4718a.png");
  background-size: cover;
  padding-bottom: 60px;
}

.header--library__buttons {
  justify-content: center;
  gap: 20px;
  padding-top: 12px;
  display: flex;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding-inline: 1.25rem;
}

@media screen and (min-width: 768px) {
  .container {
    min-width: 768px;
  }
}

@media screen and (min-width: 768px) and (min-width: 1280px) {
  .container {
    min-width: 1280px;
  }
}

a {
  color: #fff;
  text-decoration: none;
}

.button {
  text-align: center;
  width: 130px;
  border: 1px solid #fff;
  border-radius: 5px;
  padding-top: 14px;
  padding-bottom: 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1rem;
}

.button:hover, .button:focus {
  cursor: pointer;
  color: #fff;
  background-color: #ff6b01;
  border: 1px solid #0000;
  box-shadow: 0 8px 43px #ff6b0199;
}

.button--library {
  color: #fff;
  background: none;
}

.button--active {
  cursor: pointer;
  color: #fff;
  background-color: #ff6b01;
  border: none;
  box-shadow: 0 8px 43px #ff6b0199;
}

.footer {
  background-color: var(--color-gray-background-footer);
  padding-block: 30px;
}

@media screen and (min-width: 768px) {
  .footer {
    justify-content: center;
    align-items: center;
    gap: 5px;
    display: flex;
  }
}

.footer__wrapper {
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .footer__wrapper {
    margin-top: 0;
  }
}

.footer__text {
  font-size: var(--font-14);
  text-align: center;
  margin-block: 0;
}

.footer__text--team {
  cursor: pointer;
  text-decoration: underline;
}

.footer__icon {
  fill: var(--color-orange);
}

.main {
  max-width: 1280px;
  max-width: 320px;
  justify-content: center;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .main {
    max-width: 768px;
    flex-wrap: wrap;
    gap: 32px;
    padding: 27px 32px 61px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .main {
    max-width: 1280px;
    gap: 10px;
    padding: 50px 32px 60px;
  }
}

.main__list {
  list-style: none;
}

.main__list-item {
  padding: 20px 20px 0;
}

@media screen and (min-width: 768px) {
  .main__list-item {
    padding: 0;
  }
}

@media screen and (min-width: 1280px) {
  .main__movie {
    padding-bottom: 22px;
  }
}

.main__image {
  width: 280px;
  height: 402px;
  background-color: #0ff;
  border-radius: 5px;
  display: block;
}

@media screen and (min-width: 768px) {
  .main__image {
    width: 336px;
    height: 455px;
  }
}

@media screen and (min-width: 1280px) {
  .main__image {
    width: 395px;
    height: 574px;
  }
}

.main__caption {
  flex-direction: column;
  padding-top: 10px;
  display: flex;
}

.main__movie-name {
  font-size: var(--font-12);
  line-height: var(--line-height-large);
  color: var(--color-black);
  text-transform: uppercase;
}

@media screen and (min-width: 1280px) {
  .main__movie-name {
    font-size: var(--font-20);
    line-height: var(--line-height-medium);
  }
}

.main__movie-genres {
  font-size: var(--font-12);
  line-height: var(--line-height-small);
  color: var(--color-orange);
}

@media screen and (min-width: 768px) {
  .main__movie-genres {
    line-height: var(--line-height-large);
  }
}

@media screen and (min-width: 1280px) {
  .main__movie-genres {
    font-size: var(--font-20);
    line-height: var(--line-height-medium);
  }
}

.loader {
  width: 120px;
  height: 120px;
  z-index: 9999;
  border: 16px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin-top: -60px;
  margin-left: -60px;
  animation: 2s linear infinite spin;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

:root {
  --font: "Roboto", sans-serif;
  --bg-color-header: #10181d;
  --bg-color-orange: #ff6b01;
  --color-white: #fff;
  --color-black: #000;
  --color-orange: #ff6b08;
  --color-red: #ff001b;
  --color-gray-footer: #545454;
  --color-gray-background-footer: #f7f7f7;
  --color-gray-modal: #8c8c8c;
  --font-12: 12px;
  --font-14: 14px;
  --font-20: 20px;
  --font-30: 30px;
  --line-height-small: 1.17;
  --line-height-medium: 1.2;
  --line-height-large: 1.33;
  --line-height-extra-large: 1.67;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font);
  background-color: var(--color-white);
  margin: 0 auto;
  font-weight: 500;
}

